<template>
  <div class="tabbar-conent">
    <van-tabbar v-model="active" route>
      <van-tabbar-item icon-prefix="icon" icon='shouye' to="/">首页</van-tabbar-item>
      <van-tabbar-item icon-prefix="icon" icon="wenda" to="/qa">问答</van-tabbar-item>
      <van-tabbar-item icon-prefix="icon" icon="shipin" to="/video">视频</van-tabbar-item>
      <van-tabbar-item icon-prefix="icon" icon="wode" to="/my">我的</van-tabbar-item>
    </van-tabbar>
    <!--tabbar里显示子路由页面-->
    <!-- 使用keep-alive缓存组件状态 -->
    <keep-alive>
      <router-view />
    </keep-alive>
  </div>
</template>

<script>
export default {
  name: 'TabbarIndex',
  data () {
    return {
      active: 0
    }
  },
  // 组件加载完毕后
  mounted () {
    // 加载完成后就再次把缓存添加进来（因为需要缓存的为多数， 方便）
    this.$store.commit('addCould', 'TabbarIndex')
  }
}
</script>

